
.container {
    composes: container from './../../App.css';
    flex-direction: column;
    text-align: left;
    max-width: 30rem;
}
.container > article {
    font-size: 18px;
    padding: 10px 0px;
    margin-bottom: 10px;
}
.banner {
    composes: banner from './../../App.css';
}
.bannerContent {
    composes: container from './../../App.css';
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    min-height: 150vh;
    padding-top: 30px;
    padding-bottom: 30px;
    flex-direction: row;
    background-color: rgba(0, 0, 0, 0.4);
}
.mainPict {
    position: relative;
    margin-top: 15%;
    margin-right: 15%;
    margin-bottom: 15%;
    z-index: 2;
    transition: transform 0.5s;
    -webkit-transition: transform 0.5s;
    -moz-transition: transform 0.5s;
    -o-transition: transform 0.5s;
}

.mainPict:before {
    content: '';
    position: absolute;
    top: -6%;
    left: 3%;
    height: 107%;
    width: 100%;
    border: 15px solid #00004b;
    box-shadow: 0px 0px 5px 0px #00004b;
    z-index: -1;
}
.mainPict > img{
    max-width: 100%;
    border-style: none;
    box-shadow: 0px 0px 15px 0px #00004b;
}
.containerIform {
    composes: container;
    padding-left: 0px;
}
.containerIform > span {
    padding: 10px 0px;
    font-size: 20px;
    border-bottom: 1px solid #8f857d;
}
.title {
    position: relative;
    font-family: 'Montserrrat', sans-serif;
    max-width: min-content;
}
.title span {
    z-index: 2;
    max-width: min-content;
    position: relative;
}
.title span:nth-child(even):before{
    content: "";
    position: absolute;
    width: 100%;
    bottom: 0;
    height: 15px;
    background-color: #00004b;
    box-shadow: 0px 0px 5px 0px #00004b;
    z-index: -1;
}